/**
 * æ•°æ®åˆå§‹åŒ–ï¼šinitPropsã€initMethodã€initDataã€initComputedã€initWatch
 */

import { observe } from "./observer/index";
import { isFunction } from "./utils";

export function initState(vm) {
  const opts = vm.$options;
  if (opts.props) {
    initProps(vm);
  }
  if (opts.methods) {
    initMethod(vm);
  }
  if (opts.data) {
    // åˆå§‹åŒ–data
    initData(vm);
  }
  if (opts.computed) {
    initComputed(vm);
  }
  if (opts.watch) {
    initWatch(vm);
  }

  function initProps() {}
  function initMethod() {}
  function initData(vm) {
    let data = vm.$options.data;
    // å¾€å®ä¾‹ä¸Šæ·»åŠ ä¸€ä¸ªå±æ€§ _dataï¼Œå³ä¼ å…¥çš„data
    // vueç»„ä»¶dataæ¨èä½¿ç”¨å‡½æ•° é˜²æ­¢æ•°æ®åœ¨ç»„ä»¶ä¹‹é—´å…±äº«
    data = vm._data = isFunction(data) ? data.call(vm) : data;

    // å°†vm._dataä¸Šçš„æ‰€æœ‰å±æ€§ä»£ç†åˆ° vm ä¸Š
    for (let key in data) {
      proxy(vm, "_data", key);
    }
    // å¯¹æ•°æ®è¿›è¡Œè§‚æµ‹ -- æ•°æ®å“åº”å¼
    observe(data);
  }
  function initComputed() {}
  function initWatch() {}

  // å°†vm._dataä¸Šçš„å±æ€§ä»£ç†åˆ° vm ä¸Š
  function proxy(vm, source, key) {
    Object.defineProperty(vm, key, {
      get() {
        return vm[source][key];
      },
      set(newValue) {
        vm[source][key] = newValue;
      },
    });
  }
}
